'use client'

import { useState, useEffect, use } from 'react'
import { useRouter } from 'next/navigation'
import WorkflowBuilder from '@/components/accelerator/WorkflowBuilder'
import { Workflow, WorkflowTrigger, WorkflowAction, WorkflowExecution } from '@/lib/accelerator/types'

interface PageProps {
  params: Promise<{ workflowId: string }>
}

export default function WorkflowEditPage({ params }: PageProps) {
  const { workflowId } = use(params)
  const router = useRouter()
  
  // State
  const [workflow, setWorkflow] = useState<Workflow | null>(null)
  const [activeTab, setActiveTab] = useState<'builder' | 'history' | 'analytics' | 'settings'>('builder')
  const [loading, setLoading] = useState(true)
  const [saving, setSaving] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [successMessage, setSuccessMessage] = useState<string | null>(null)
  
  // Execution history
  const [executions, setExecutions] = useState<WorkflowExecution[]>([])
  const [executionsLoading, setExecutionsLoading] = useState(false)
  
  // Settings form
  const [workflowName, setWorkflowName] = useState('')
  const [workflowDescription, setWorkflowDescription] = useState('')
  const [allowMultipleExecutions, setAllowMultipleExecutions] = useState(true)
  const [maxExecutionsPerContact, setMaxExecutionsPerContact] = useState(1)
  
  // TODO: Get from auth context
  const subAccountId = 'b0000000-0000-0000-0000-000000000001'

  // Load workflow on mount
  useEffect(() => {
    loadWorkflow()
  }, [workflowId])

  // Load executions when switching to history tab
  useEffect(() => {
    if (activeTab === 'history' && executions.length === 0) {
      loadExecutions()
    }
  }, [activeTab])

  const loadWorkflow = async () => {
    setLoading(true)
    try {
      const res = await fetch(`/api/v1/workflows?id=${workflowId}&subAccountId=${subAccountId}`)
      const data = await res.json()
      
      if (data.success && data.data) {
        setWorkflow(data.data)
        setWorkflowName(data.data.name)
        setWorkflowDescription(data.data.description || '')
        setAllowMultipleExecutions(data.data.allowMultipleExecutions ?? true)
        setMaxExecutionsPerContact(data.data.maxExecutionsPerContact ?? 1)
      } else {
        setError('Workflow not found')
      }
    } catch (err) {
      console.error('Error loading workflow:', err)
      setError('Failed to load workflow')
    } finally {
      setLoading(false)
    }
  }

  const loadExecutions = async () => {
    setExecutionsLoading(true)
    try {
      const res = await fetch(`/api/v1/workflows/${workflowId}/executions?limit=50`)
      const data = await res.json()
      if (data.success) {
        setExecutions(data.data || [])
      }
    } catch (err) {
      console.error('Error loading executions:', err)
    } finally {
      setExecutionsLoading(false)
    }
  }

  const handleSaveWorkflow = async (trigger: WorkflowTrigger, actions: WorkflowAction[]) => {
    setSaving(true)
    setError(null)

    try {
      const res = await fetch('/api/v1/workflows', {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          id: workflowId,
          subAccountId,
          name: workflowName,
          description: workflowDescription,
          trigger,
          actions: actions.map(a => ({
            id: a.id,
            type: a.type,
            name: a.name,
            config: a.config,
            delay: a.delay,
            continueOnError: a.continueOnError,
            order: a.order,
            conditions: a.conditions
          })),
          allowMultipleExecutions,
          maxExecutionsPerContact
        })
      })

      const data = await res.json()
      
      if (data.success) {
        setSuccessMessage('Workflow saved successfully!')
        setTimeout(() => setSuccessMessage(null), 3000)
        await loadWorkflow()
      } else {
        setError(data.error || 'Failed to save workflow')
      }
    } catch (err) {
      console.error('[WorkflowEditPage] Error saving workflow:', err)
      setError('Failed to save workflow')
    } finally {
      setSaving(false)
    }
  }

  const handleSaveSettings = async () => {
    setSaving(true)
    setError(null)

    try {
      const res = await fetch('/api/v1/workflows', {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          id: workflowId,
          subAccountId,
          name: workflowName,
          description: workflowDescription,
          allowMultipleExecutions,
          maxExecutionsPerContact
        })
      })

      const data = await res.json()
      
      if (data.success) {
        setSuccessMessage('Settings saved!')
        setTimeout(() => setSuccessMessage(null), 3000)
        loadWorkflow()
      } else {
        setError(data.error || 'Failed to save settings')
      }
    } catch (err) {
      console.error('Error saving settings:', err)
      setError('Failed to save settings')
    } finally {
      setSaving(false)
    }
  }

  const handleToggleStatus = async () => {
    if (!workflow) return
    
    const newStatus = workflow.status === 'active' ? 'inactive' : 'active'
    
    try {
      const res = await fetch('/api/v1/workflows', {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          id: workflowId,
          subAccountId,
          status: newStatus
        })
      })

      const data = await res.json()
      
      if (data.success) {
        setSuccessMessage(`Workflow ${newStatus === 'active' ? 'activated' : 'deactivated'}!`)
        setTimeout(() => setSuccessMessage(null), 3000)
        loadWorkflow()
      }
    } catch (err) {
      console.error('Error toggling status:', err)
    }
  }

  const handleDeleteWorkflow = async () => {
    if (!confirm('Are you sure you want to delete this workflow? This action cannot be undone.')) {
      return
    }

    try {
      const res = await fetch(`/api/v1/workflows?id=${workflowId}&subAccountId=${subAccountId}&permanent=true`, {
        method: 'DELETE'
      })

      const data = await res.json()
      
      if (data.success) {
        router.push('/sub-account/accelerators')
      } else {
        setError(data.error || 'Failed to delete workflow')
      }
    } catch (err) {
      console.error('Error deleting workflow:', err)
      setError('Failed to delete workflow')
    }
  }

  const handleCancel = () => {
    router.push('/sub-account/accelerators')
  }

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'active': return 'bg-green-100 text-green-800'
      case 'inactive': return 'bg-gray-100 text-gray-800'
      case 'draft': return 'bg-yellow-100 text-yellow-800'
      default: return 'bg-gray-100 text-gray-800'
    }
  }

  const getExecutionStatusColor = (status: string) => {
    switch (status) {
      case 'completed': return 'bg-green-100 text-green-800'
      case 'running': return 'bg-blue-100 text-blue-800'
      case 'failed': return 'bg-red-100 text-red-800'
      case 'cancelled': return 'bg-gray-100 text-gray-800'
      default: return 'bg-gray-100 text-gray-800'
    }
  }

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto mb-4"></div>
          <p className="text-gray-600">Loading workflow...</p>
        </div>
      </div>
    )
  }

  if (error && !workflow) {
    return (
      <div className="max-w-2xl mx-auto py-12 px-4 text-center">
        <div className="w-16 h-16 bg-red-100 rounded-full flex items-center justify-center mx-auto mb-4">
          <i className="fas fa-exclamation-triangle text-red-600 text-2xl"></i>
        </div>
        <h2 className="text-xl font-bold text-gray-900 mb-2">Workflow Not Found</h2>
        <p className="text-gray-600 mb-4">{error}</p>
        <button
          onClick={() => router.push('/sub-account/accelerators')}
          className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700"
        >
          Back to Workflows
        </button>
      </div>
    )
  }

  if (!workflow) return null

  return (
    <div className="min-h-[calc(100vh-200px)] flex flex-col">
      {/* Header */}
      <div className="bg-white border-b px-6 py-4">
        <div className="flex items-center justify-between">
          <div className="flex items-center gap-4">
            <button
              onClick={handleCancel}
              className="text-gray-600 hover:text-gray-800"
            >
              <i className="fas fa-arrow-left"></i>
            </button>
            <div>
              <div className="flex items-center gap-3">
                <h1 className="text-xl font-bold text-gray-900">{workflow.name}</h1>
                <span className={`px-2 py-1 rounded text-xs font-medium ${getStatusColor(workflow.status)}`}>
                  {workflow.status}
                </span>
              </div>
              {workflow.description && (
                <p className="text-sm text-gray-500 mt-1">{workflow.description}</p>
              )}
            </div>
          </div>
          
          <div className="flex items-center gap-3">
            <button
              onClick={handleToggleStatus}
              className={`px-4 py-2 rounded-lg flex items-center gap-2 ${
                workflow.status === 'active'
                  ? 'bg-yellow-100 text-yellow-700 hover:bg-yellow-200'
                  : 'bg-green-100 text-green-700 hover:bg-green-200'
              }`}
            >
              <i className={`fas fa-${workflow.status === 'active' ? 'pause' : 'play'}`}></i>
              {workflow.status === 'active' ? 'Deactivate' : 'Activate'}
            </button>
          </div>
        </div>

        {/* Tabs */}
        <div className="flex gap-6 mt-4 -mb-4">
          {[
            { id: 'builder', label: 'Builder', icon: 'project-diagram' },
            { id: 'history', label: 'Execution History', icon: 'history' },
            { id: 'analytics', label: 'Analytics', icon: 'chart-line' },
            { id: 'settings', label: 'Settings', icon: 'cog' }
          ].map(tab => (
            <button
              key={tab.id}
              onClick={() => setActiveTab(tab.id as any)}
              className={`px-4 py-3 text-sm font-medium border-b-2 transition-colors ${
                activeTab === tab.id
                  ? 'border-blue-600 text-blue-600'
                  : 'border-transparent text-gray-600 hover:text-gray-900'
              }`}
            >
              <i className={`fas fa-${tab.icon} mr-2`}></i>
              {tab.label}
            </button>
          ))}
        </div>
      </div>

      {/* Content */}
      <div className="flex-1 overflow-hidden min-h-[500px]">
        {/* Builder Tab */}
        {activeTab === 'builder' && (
          <WorkflowBuilder
            key={`${workflowId}-${workflow.version || 0}`}
            workflowId={workflowId}
            initialTrigger={workflow.trigger}
            initialActions={workflow.actions}
            onSave={handleSaveWorkflow}
            onCancel={handleCancel}
          />
        )}

        {/* History Tab */}
        {activeTab === 'history' && (
          <div className="p-6 overflow-y-auto h-full">
            <div className="max-w-4xl mx-auto">
              <div className="flex items-center justify-between mb-6">
                <h2 className="text-lg font-semibold text-gray-900">Execution History</h2>
                <button
                  onClick={loadExecutions}
                  className="text-blue-600 hover:text-blue-700 text-sm flex items-center gap-2"
                >
                  <i className="fas fa-sync-alt"></i>
                  Refresh
                </button>
              </div>

              {executionsLoading ? (
                <div className="text-center py-12">
                  <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600 mx-auto mb-4"></div>
                  <p className="text-gray-500">Loading executions...</p>
                </div>
              ) : executions.length === 0 ? (
                <div className="text-center py-12 bg-white rounded-xl border">
                  <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mx-auto mb-4">
                    <i className="fas fa-history text-gray-400 text-2xl"></i>
                  </div>
                  <h3 className="font-medium text-gray-900 mb-2">No executions yet</h3>
                  <p className="text-gray-500">
                    When this workflow runs, execution history will appear here.
                  </p>
                </div>
              ) : (
                <div className="bg-white rounded-xl border divide-y">
                  {executions.map(execution => (
                    <div key={execution.id} className="p-4 hover:bg-gray-50">
                      <div className="flex items-center justify-between">
                        <div className="flex items-center gap-4">
                          <span className={`px-2 py-1 rounded text-xs font-medium ${getExecutionStatusColor(execution.status)}`}>
                            {execution.status}
                          </span>
                          <div>
                            <p className="text-sm font-medium text-gray-900">
                              {execution.triggerType.replace(/_/g, ' ')}
                            </p>
                            <p className="text-xs text-gray-500">
                              {new Date(execution.triggeredAt).toLocaleString()}
                            </p>
                          </div>
                        </div>
                        <div className="text-right">
                          <p className="text-sm text-gray-600">
                            {execution.actionResults?.length || 0} actions
                          </p>
                          {execution.completedAt && (
                            <p className="text-xs text-gray-500">
                              Duration: {Math.round((new Date(execution.completedAt).getTime() - new Date(execution.startedAt!).getTime()) / 1000)}s
                            </p>
                          )}
                        </div>
                      </div>
                      {execution.errorMessage && (
                        <div className="mt-3 p-3 bg-red-50 rounded-lg">
                          <p className="text-sm text-red-600">
                            <i className="fas fa-exclamation-circle mr-2"></i>
                            {execution.errorMessage}
                          </p>
                        </div>
                      )}
                    </div>
                  ))}
                </div>
              )}
            </div>
          </div>
        )}

        {/* Analytics Tab */}
        {activeTab === 'analytics' && (
          <div className="p-6 overflow-y-auto h-full">
            <div className="max-w-4xl mx-auto">
              <h2 className="text-lg font-semibold text-gray-900 mb-6">Workflow Analytics</h2>
              
              {/* Stats Cards */}
              <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mb-8">
                <div className="bg-white rounded-xl border p-4">
                  <div className="flex items-center gap-3">
                    <div className="w-10 h-10 bg-blue-100 rounded-lg flex items-center justify-center">
                      <i className="fas fa-rocket text-blue-600"></i>
                    </div>
                    <div>
                      <p className="text-2xl font-bold text-gray-900">{workflow.totalExecutions}</p>
                      <p className="text-sm text-gray-500">Total Runs</p>
                    </div>
                  </div>
                </div>
                
                <div className="bg-white rounded-xl border p-4">
                  <div className="flex items-center gap-3">
                    <div className="w-10 h-10 bg-green-100 rounded-lg flex items-center justify-center">
                      <i className="fas fa-check text-green-600"></i>
                    </div>
                    <div>
                      <p className="text-2xl font-bold text-gray-900">{workflow.successfulExecutions}</p>
                      <p className="text-sm text-gray-500">Successful</p>
                    </div>
                  </div>
                </div>
                
                <div className="bg-white rounded-xl border p-4">
                  <div className="flex items-center gap-3">
                    <div className="w-10 h-10 bg-red-100 rounded-lg flex items-center justify-center">
                      <i className="fas fa-times text-red-600"></i>
                    </div>
                    <div>
                      <p className="text-2xl font-bold text-gray-900">{workflow.failedExecutions}</p>
                      <p className="text-sm text-gray-500">Failed</p>
                    </div>
                  </div>
                </div>
                
                <div className="bg-white rounded-xl border p-4">
                  <div className="flex items-center gap-3">
                    <div className="w-10 h-10 bg-purple-100 rounded-lg flex items-center justify-center">
                      <i className="fas fa-percentage text-purple-600"></i>
                    </div>
                    <div>
                      <p className="text-2xl font-bold text-gray-900">
                        {workflow.totalExecutions > 0 
                          ? Math.round((workflow.successfulExecutions / workflow.totalExecutions) * 100) 
                          : 0}%
                      </p>
                      <p className="text-sm text-gray-500">Success Rate</p>
                    </div>
                  </div>
                </div>
              </div>

              {/* Last Executed */}
              <div className="bg-white rounded-xl border p-6">
                <h3 className="font-semibold text-gray-900 mb-4">Workflow Performance</h3>
                <div className="space-y-4">
                  <div className="flex justify-between items-center py-3 border-b">
                    <span className="text-gray-600">Last Executed</span>
                    <span className="font-medium text-gray-900">
                      {workflow.lastExecutedAt 
                        ? new Date(workflow.lastExecutedAt).toLocaleString() 
                        : 'Never'}
                    </span>
                  </div>
                  <div className="flex justify-between items-center py-3 border-b">
                    <span className="text-gray-600">Created</span>
                    <span className="font-medium text-gray-900">
                      {new Date(workflow.createdAt).toLocaleString()}
                    </span>
                  </div>
                  <div className="flex justify-between items-center py-3 border-b">
                    <span className="text-gray-600">Last Updated</span>
                    <span className="font-medium text-gray-900">
                      {new Date(workflow.updatedAt).toLocaleString()}
                    </span>
                  </div>
                  <div className="flex justify-between items-center py-3">
                    <span className="text-gray-600">Version</span>
                    <span className="font-medium text-gray-900">v{workflow.version}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}

        {/* Settings Tab */}
        {activeTab === 'settings' && (
          <div className="p-6 overflow-y-auto h-full">
            <div className="max-w-2xl mx-auto space-y-6">
              <h2 className="text-lg font-semibold text-gray-900">Workflow Settings</h2>

              {/* Basic Info */}
              <div className="bg-white rounded-xl border p-6">
                <h3 className="font-semibold text-gray-900 mb-4">Basic Information</h3>
                <div className="space-y-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">Name</label>
                    <input
                      type="text"
                      value={workflowName}
                      onChange={(e) => setWorkflowName(e.target.value)}
                      className="w-full px-4 py-2 border border-gray-300 rounded-lg"
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">Description</label>
                    <textarea
                      value={workflowDescription}
                      onChange={(e) => setWorkflowDescription(e.target.value)}
                      rows={3}
                      className="w-full px-4 py-2 border border-gray-300 rounded-lg"
                    />
                  </div>
                </div>
              </div>

              {/* Execution Settings */}
              <div className="bg-white rounded-xl border p-6">
                <h3 className="font-semibold text-gray-900 mb-4">Execution Settings</h3>
                <div className="space-y-4">
                  <div className="flex items-center justify-between py-3">
                    <div>
                      <p className="font-medium text-gray-900">Allow Multiple Executions</p>
                      <p className="text-sm text-gray-500">Allow same contact to trigger multiple times</p>
                    </div>
                    <label className="relative inline-flex items-center cursor-pointer">
                      <input
                        type="checkbox"
                        checked={allowMultipleExecutions}
                        onChange={(e) => setAllowMultipleExecutions(e.target.checked)}
                        className="sr-only peer"
                      />
                      <div className="w-11 h-6 bg-gray-200 peer-focus:outline-none peer-focus:ring-4 peer-focus:ring-blue-300 rounded-full peer peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-[2px] after:left-[2px] after:bg-white after:border-gray-300 after:border after:rounded-full after:h-5 after:w-5 after:transition-all peer-checked:bg-blue-600"></div>
                    </label>
                  </div>
                  
                  {allowMultipleExecutions && (
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1">
                        Max Executions Per Contact
                      </label>
                      <input
                        type="number"
                        min="1"
                        value={maxExecutionsPerContact}
                        onChange={(e) => setMaxExecutionsPerContact(parseInt(e.target.value) || 1)}
                        className="w-32 px-4 py-2 border border-gray-300 rounded-lg"
                      />
                      <p className="text-sm text-gray-500 mt-1">0 = unlimited</p>
                    </div>
                  )}
                </div>
              </div>

              {/* Save Button */}
              <div className="flex justify-end gap-3">
                <button
                  onClick={handleSaveSettings}
                  disabled={saving}
                  className="px-6 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:opacity-50 flex items-center gap-2"
                >
                  {saving && <i className="fas fa-spinner fa-spin"></i>}
                  Save Settings
                </button>
              </div>

              {/* Danger Zone */}
              <div className="bg-red-50 border border-red-200 rounded-xl p-6">
                <h3 className="font-semibold text-red-800 mb-2">Danger Zone</h3>
                <p className="text-sm text-red-600 mb-4">
                  Deleting this workflow is permanent and cannot be undone.
                </p>
                <button
                  onClick={handleDeleteWorkflow}
                  className="px-4 py-2 bg-red-600 text-white rounded-lg hover:bg-red-700 flex items-center gap-2"
                >
                  <i className="fas fa-trash"></i>
                  Delete Workflow
                </button>
              </div>
            </div>
          </div>
        )}
      </div>

      {/* Success Toast */}
      {successMessage && (
        <div className="fixed bottom-4 right-4 bg-green-600 text-white px-4 py-3 rounded-lg shadow-lg z-50">
          <div className="flex items-center gap-3">
            <i className="fas fa-check-circle"></i>
            <span>{successMessage}</span>
          </div>
        </div>
      )}

      {/* Error Toast */}
      {error && (
        <div className="fixed bottom-4 right-4 bg-red-600 text-white px-4 py-3 rounded-lg shadow-lg z-50">
          <div className="flex items-center gap-3">
            <i className="fas fa-exclamation-circle"></i>
            <span>{error}</span>
            <button onClick={() => setError(null)} className="ml-2">
              <i className="fas fa-times"></i>
            </button>
          </div>
        </div>
      )}

      {/* Saving Overlay */}
      {saving && activeTab === 'builder' && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
          <div className="bg-white rounded-xl p-8 text-center">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto mb-4"></div>
            <p className="text-gray-600">Saving workflow...</p>
          </div>
        </div>
      )}
    </div>
  )
}
